.collections-slider {
  position: relative;
  flex-grow: 1;
  width: 100%;

  &__item {
    display: inline-flex;
    flex-direction: column;
    position: relative;
    padding: 60px 0 0;
  }

  &__info {
    @include flex_column-center;
    align-self: center;
    color: var(--text-color);
    max-width: 510px;
    width: 100%;
    position: absolute;
    top: 0;

    h3 {
      @include list-reset;
      font-family: $robotoRegular;
      font-weight: 400;
      font-size: var(--heading-font-size);
      line-height: 0.88;
      padding-bottom: var(--padding-bottom);
    }

    p {
      @include list-reset;
      font-family: $robotoLight;
      font-weight: 300;
      font-size: var(--text-font-size);
      line-height: 2;
      text-align: center;

      &:last-of-type {
        padding-bottom: var(--padding-bottom);
      }
    }
  }

  &__images {
    @include flex_s-between;
    padding: 0 80px;

    div {
      position: relative;
      &:first-of-type,
      &:last-of-type{
        &:before {
          content: '';
          position: absolute;
          bottom: 40px;
          width: 457px;
          height: 131px;
          z-index: -1;
          background-repeat: no-repeat;
        }
      }

      &:first-of-type {
        &:before {
          left: -80px;
        }
      }

      &:last-of-type {
        &:before {
          right: -80px;
        }
      }

      img {
        max-height: 500px;
        object-fit: contain;
      }
    }

    &--velvet {
      div {
        &:first-of-type {
          &:before {
            background-image: url("../img/velvet__cabernet--bg.png");
            background-image: -webkit-image-set(url('../img/velvet__cabernet--bg.webp') 1x);
            @include retina {
              background-image: url("../img/velvet__cabernet--bg@2x.png");
              background-image: -webkit-image-set(url('../img/velvet__cabernet--bg@2x.webp') 2x);
            }
          }
        }

        &:last-of-type {
          &:before {
            background-image: url("../img/velvet__muscat--bg.png");
            background-image: -webkit-image-set(url('../img/velvet__muscat--bg.webp') 1x);
            @include retina {
              background-image: url("../img/velvet__muscat--bg@2x.png");
              background-image: -webkit-image-set(url('../img/velvet__muscat--bg@2x.webp') 2x);
            }
          }
        }
      }
    }

    &--vintage {
      div {
        &:first-of-type,
        &:last-of-type {
          &:before {
            background-image: url("../img/vintage__cabernet--bg.png");
            background-image: -webkit-image-set(url('../img/vintage__cabernet--bg.webp') 1x);
            @include retina {
              background-image: url("../img//vintage__cabernet--bg@2x.png");
              background-image: -webkit-image-set(url('../img/vintage__cabernet--bg@2x.webp') 2x);
            }
          }
        }

        &:last-of-type {
          &:before {
            background-image: url("../img/vintage__chardonnay--bg.png");
            background-image: -webkit-image-set(url('../img/vintage__chardonnay--bg.webp') 1x);
            @include retina {
              background-image: url("../img/vintage__chardonnay--bg@2x.png");
              background-image: -webkit-image-set(url('../img/vintage__chardonnay--bg@2x.webp') 2x);
            }
          }
        }
      }
    }
  }

  &__btn {
    color: var(--secondary-color);

    span{
      color: var(--secondary-color);
    }
  }
}
