:root {
  --text-font-size: 1.125rem;
  --heading-font-size: 1.333rem;
  --line-height: 30px;
  --padding-bottom: 30px;
  --border: 2px solid;
  --primary-color: #e9e9d8;
  --secondary-color: #903b57;
  --text-color: #514f55;
  --bg-color: #ffffff;
  --transition: .35s cubic-bezier(0.3, 1, 0.89, 1);
}

::selection {
  background-color: var(--text-color);
  color: var(--primary-color);
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
  height: 100%;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  @include flex_column-center;
  position: relative;
  margin: 0;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  height: 100%;
  font-family: $robotoLight;
  font-size: var(--text-font-size);
  font-weight: 300;
  background-color: var(--bg-color);
  text-rendering: optimizeLegibility;
  overflow: hidden;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  transition: var(--transition);

  &:active {
    opacity: 0.8;
  }
}

img {
  display: block;
  max-width: 100%;
  height: 100%;
}

main {
  flex: 1 0 auto;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  margin: -1px;
  padding: 0;
  width: 1px;
  height: 1px;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.overflow {
  overflow: hidden;
}

.page-wrapper {
  @include flex_column;
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  position: relative;
}

.page-footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1440px;
  width: 100%;
  margin: 0 auto;
  padding: 0 30px;

  @include mobilePortrait {
    padding: 0 20px;
  }
}

.btn {
  --btn-height: 65px;
  @include btn-reset;
  @include flex_center;
  position: relative;
  height: var(--btn-height);
  font-family: $robotoRegular;
  font-size: var(--text-font-size);
  font-weight: 400;
  line-height: var(--btn-line-height);
  text-align: center;
  background-color: transparent;
  transition: var(--transition);

  svg {
    position: absolute;
    top: 0;
    left: 0;

    rect {
      stroke-width: 2;
      transition: var(--transition);
    }
  }

  &:hover svg rect,
  &:focus svg rect {
    stroke-width: 2;
  }

  &--common {
    width: 195px;

    svg {
      rect {
        stroke-dasharray: 290, 0;
        stroke-dashoffset: 0;
      }
    }

    &:hover svg rect,
    &:focus svg rect {
      stroke-dasharray: 103, 545;
      stroke-dashoffset: 415;
    }
  }
}

.heading {
  @include list-reset;
  font-family: $robotoSlabRegular;
  font-size: var(--heading-font-size);
  font-weight: 400;
  //line-height: var(--line-height);

  @include mobileLandscape {
    font-size: 22px;
  }
}

.text {
  @include list-reset;
  font-family: $robotoLight;
  font-size: var(--text-font-size);
  font-weight: 300;
  //line-height: var(--line-height);

  @include mobileLandscape {
    font-size: 16px;
  }
}

.underline {
  display: inline-block;
  position: relative;
  width: max-content;
  font-family: $robotoLight;
  font-weight: 300;
  font-size: var(--text-font-size);
  line-height: var(--line-height);
  //line-height: 1.173;

  &:before {
    content: "";
    width: 0;
    height: 2px;
    position: absolute;
    bottom: 0;
    right: 0;
    background-color: var(--secondary-color);
    transition: width var(--transition);
  }

  &:hover:before {
    width: 100%;
    right: auto;
    left: 0;
  }

  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    width: 0;
    height: 2px;
    right: 0;
    background-color: var(--secondary-color);
    transition: width var(--transition);
  }

  &:hover,
  &:focus {
    outline: none;
    color: var(--secondary-color);

    &::after {
      opacity: 1;
      background-color: var(--secondary-color);
    }
  }
}

.line {
  @include list-reset;
  color: var(--secondary-color);
  display: flex;
  align-items: center;
  width: 45%;
  margin-left: auto;
  margin-right: auto;
  &:before,
  &:after {
    content: '';
    height: 1px;
    background: var(--text-color);
    flex-grow: 1;
  }

  &:before {
    margin-right: 10px;
  }
  &:after {
    margin-left: 10px;
  }
}

