.page-header {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 100%;

  &--product {
    position: relative;
  }

  &__nav {
    @include flex_s-between;
    min-height: 90px;
    background-color: transparent;
    padding-top: 40px;
    padding-bottom: 30px;
    @include tabletPortrait {
      min-height: 50px;
      align-items: center;
      padding-top: 10px;
      padding-bottom: 10px;
      background-color: rgba(233, 233, 216, 0.7);
    }
    @include tabletLandscape {
      min-height: 50px;
      align-items: center;
      padding-top: 10px;
      padding-bottom: 10px;
      background-color: rgba(233, 233, 216, 0.7);
    }
    @include mobileLandscape {
      min-height: 50px;
      align-items: center;
      padding-top: 10px;
      padding-bottom: 10px;
      background-color: rgba(233, 233, 216, 0.7);
    }
  }

  &__toggler {
    display: none;
    @include btn-reset;
    width: 28px;
    height: 24px;
    position: relative;
    z-index: 10;

    span {
      position: relative;
      display: block;
      width: 100%;
      height: 3.2px;
      background-color: var(--text-color);
      transition: var(--transition);
      backface-visibility: hidden;
      border-radius: 9px;
      &::before,
      &::after {
        content: "";
        position: absolute;
        left: 0;
        top: 9px;
        width: 100%;
        height: 3.3px;
        background-color: var(--text-color);
        transition: var(--transition);
        backface-visibility: hidden;
        border-radius: 9px;
      }
      &::after {
        top: 18px;
      }
    }

    @include tabletPortrait {
      display: block;
    }
    @include tabletLandscape {
      display: block;
    }
    @include mobileLandscape {
      display: block;
    }

    &.on {
      span {
        background-color: transparent;
        &::before {
          transform: rotate(45deg) translate(-1px, 0px);
        }
        &::after {
          transform: rotate(-45deg) translate(6px, -7px);
        }
      }
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    position: relative;
    user-select: none;

    img {
      width: 150px;
      height: 100%;

      @include mobilePortrait {
        width: 90px;
      }
      @include mobileLandscape {
        width: 90px;
      }
      @include tabletPortrait {
        width: 90px;
      }
      @include tabletLandscape {
        width: 90px;
      }
    }
  }

  &__list {
    @include list-reset;
    display: flex;
    align-items: center;

    @include tabletPortrait {
      flex-direction: column;
    }
    @include tabletLandscape {
      flex-direction: column;
    }

    li {
      &:not(:last-child) {
        margin-right: 40px;

        @include tabletPortrait {
          margin-right: 0;
          min-height: 70px;
        }
        @include tabletLandscape {
          margin-right: 0;
          min-height: 70px;
        }
      }
    }
  }

  &__link {
    padding: 0 0.5em;
    color: var(--primary-color);

    &::after {
      @include tabletPortrait {
        background-color: var(--text-color);
      }
    }

    &:hover,
    &:focus {
      &::after {
        @include tabletPortrait {
          background-color: var(--text-color);
        }
      }
    }

    @include tabletPortrait {
      color: var(--text-color);
      font-size: 1.222rem;
      font-weight: bold;
    }
    @include tabletLandscape {
      color: var(--text-color);
      font-size: 1.222rem;
      font-weight: bold;
    }

    &--lang {
      padding: 0 .8em;
      background-color: rgba(255, 210, 162, 0.3);
    }

    &--product {
      color: var(--text-color);
    }
  }

  &__menu {
    position: relative;
    transform: translate(0);
    visibility: visible;
    opacity: 1;
    display: block;
    @include tabletPortrait {
      opacity: 0;
      position: absolute;
      visibility: hidden;
      transform: translateY(-500);
      z-index: 2;
      margin: 0;

      &.active {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        visibility: visible;
        overflow: auto;
        opacity: 1;
        transform: translateY(0);
        background-color: var(--primary-color);
      }
    }
    @include tabletLandscape {
      opacity: 0;
      position: absolute;
      visibility: hidden;
      transform: translateY(-500);
      z-index: 2;
      margin: 0;

      &.active {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        visibility: visible;
        overflow: auto;
        opacity: 1;
        transform: translateY(0);
        background-color: var(--primary-color);
      }
    }
    @include mobileLandscape {
      opacity: 0;
      position: absolute;
      visibility: hidden;
      transform: translateY(-500);
      z-index: 2;
      margin: 0;

      &.active {
        display: flex;
        flex-direction: column;
        justify-content: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        visibility: visible;
        overflow: auto;
        opacity: 1;
        transform: translateY(0);
        background-color: var(--primary-color);
      }
    }
  }
}
