.product-card {
  position: relative;

  h1, h2 {
    color: var(--secondary-color);
  }

  h1 {
    padding-bottom: 15px;
  }

  h2 {
    @include list-reset;
    font-family: $robotoRegular;
    font-size: 2.5rem;
    text-transform: uppercase;
    padding-bottom: 15px;
  }

  h3 {
    font-size: var(--text-font-size);
    line-height: 1.333;
    padding-bottom: 80px;
  }

  h4 {
    @include list-reset;
    color: var(--secondary-color);
  }

  p {
    @include list-reset;
    color: var(--text-color);
  }

  &__menu {
    @include list-reset;
    @include flex_column;
    min-height: 270px;
    max-width: 240px;
    width: 100%;
    position: absolute;
    left: 0;
    top: 80px;
  }

  &__menu-item {
    padding: 0 0.5em;
    margin-bottom: 1rem;
    color: var(--text-color);
    cursor: pointer;
    &.active {
      color: var(--secondary-color);
      border-bottom: 2px solid var(--secondary-color);
      text-transform: uppercase;
      &.underline {
        cursor: default;
        &:after,
        &:before {
          position: relative;
        }
      }
    }
  }

  &__info {
    @include list-reset;
  }

  &__info-item {
    display: none;
    &.active {
      @include flex_column;
      > * {
        margin-bottom: 50px;
        padding: 1em 0;
      }
    }
  }

  &__btn {
    color: var(--secondary-color);
  }
}
