.page-footer {
  position: relative;
  width: 100%;
  min-height: 500px;
  overflow: hidden;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../img/footer-bg.png");
  background-image: -webkit-image-set(url('../img/footer-bg.webp') 1x);
  background-color: #feebc1;

  @include retina {
    background-image: url("../img/footer-bg@2x.png");
    background-image: -webkit-image-set(url('../img/footer-bg@2x.webp') 2x);
  }

  @include mobilePortrait {
    background-size: cover;
  }
  @include tabletPortrait {
    background-size: cover;
  }
  @include tabletLandscape {
    background-size: cover;
  }

  &__content {
    padding-top: 60px;
    padding-bottom: 30px;
    min-height: 200px;

    @include mobilePortrait {
      padding-top: 50px;
    }
    @include tabletPortrait {
      padding-top: 50px;
    }
  }

  &__body {
    @include flex_s-between;

    @include mobilePortrait {
      flex-direction: column;
      align-items: center;
    }
    @include tabletPortrait {
      flex-direction: column;
      align-items: center;
    }
  }

  &__logo-container {
    @include flex_column-center;
    max-width: 210px;
    width: 100%;
    flex: 0 0 210px;
    height: max-content;

    @include mobilePortrait {
      flex: 1;
      margin-bottom: 30px;
    }
    @include tabletPortrait {
      flex: 1;
      margin-bottom: 30px;
    }
    @include tabletLandscape {
      margin-right: 10px;
    }

    p {
      @include list-reset;
      font-family: $robotoLight;
      font-weight: 300;
      font-size: 14px;
      color: var(--text-color);
      padding-bottom: 5px;
    }
  }

  &__logo {
    padding-bottom: 10px;
    user-select: none;

    img {
      width: 150px;
      height: 100%;
    }
  }

  &__block-group {
    max-width: 1000px;
    display: grid;
    grid-template-columns: 250px 250px 370px;

    @include mobilePortrait {
      display: flex;
    }
    @include tabletPortrait {
      display: flex;
    }
    @include tabletLandscape {
      grid-template-columns: max-content max-content 330px;
      grid-gap: 20px;
    }
  }

  &__item {
    ul, p {
      @include list-reset;
    }

    a, p {
      color: var(--text-color);
      line-height: 1.667;
    }

    &--contacts {
      @include flex_column;
      justify-content: space-between;

    }
  }

  &__list {
    @include mobilePortrait {
      display: none;
    }
    @include tabletPortrait {
      display: none;
    }
  }

  &__link {
    padding: 0.1em 0.5em;
    margin-bottom: 0.2em;
    color: var(--text-color);

    &::after {
      @include tabletPortrait {
        background-color: var(--text-color);
      }
    }

    &:hover,
    &:focus {
      &::after {
        @include tabletPortrait {
          background-color: var(--text-color);
        }
      }
    }
  }

  &__socials {
    display: flex;
    margin-bottom: auto;

    li:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__icon {
    fill: var(--secondary-color);
    border-radius: 50%;

    &:hover,
    &:focus {
      filter: drop-shadow(2px 2px 2px var(--secondary-color));
    }
  }
}
