.description {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 1000px;
  margin-left: auto;

  &__image {
    position: relative;
    margin-right: 50px;
    &:before {
      content: '';
      position: absolute;
      bottom: -6px;
      left: -2px;
      width: 302px;
      height: 89px;
      background: url("../img/shadow.png") no-repeat;
      transform: rotate(-8deg);
      z-index: -1;
    }

    img {
      max-height: 85vh;
      min-height: 500px;
      height: 100%;
      width: 100%;
      object-fit: contain;
      //filter: drop-shadow(5px 0 5px #524e47);
    }
  }

  &__info {
    @include flex_column;
    max-width: 730px;
    width: 100%;
  }

  &__rating {
    display: flex;
    margin-bottom: 30px;
    img {
      margin-right: 10px;
    }
  }

  &__group {
    @include flex_column;

    ul {
      &:first-child {
        border-top: 1px solid var(--text-color);
        border-bottom: 1px solid var(--text-color);
      }
    }
  }

  &__list {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
  }

  &__item {
    @include flex_column;
    //flex-basis: 33.3%;
    flex-basis: 25%;

    div {
      display: flex;
      align-items: flex-end;
      padding-bottom: 20px;

      img {
        object-fit: contain;

        &.wineglass {
          width: 20px;
          height: 29px;
        }
        &.grapes {
          width: 26px;
          height: 26px;
        }
        &.pin {
          width: 26px;
          height: 26px;
        }
        &.bottle {
          width: 20px;
          height: 29px;
        }
        &.filtering {
          width: 26px;
          height: 26px;
        }
        &.barrel {
          width: 26px;
          height: 26px;
        }
      }

      span {
        color: var(--secondary-color);
        padding-left: 5px;
        font-size: 16px;
        //line-height: 1.32;
        line-height: 0.9;
      }
    }

    p {
      font-family: $robotoRegular;
      font-size: var(--text-font-size);
      line-height: 1.333;
      color: var(--text-color);
    }
  }
}
