.hero {
  @include flex_center;
  width: 100%;
  height: 100vh;
  position: relative;
  background-image: url("../img/hero.png");
  background-image: -webkit-image-set(url('../img/hero.webp') 1x);
  background-color: #c19883;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;
  @include retina {
    background-image: url("../img/hero@2x.png");
    background-image: -webkit-image-set(url('../img/hero@2x.webp') 2x);
  }

  &__content {
    @include flex_column-center;
    color: var(--primary-color);
    text-align: center;

    h1 {
      @include list-reset;
      font-family: $montserratLight;
      font-size: 3.333rem;
      font-weight: 300;
      padding-bottom: var(--padding-bottom);

      @include tabletPortrait {
        font-size: 26px;
      }
      @include tabletLandscape {
        font-size: 26px;
        padding-bottom: 20px;
      }
    }

    p {
      //--min: 0.778rem;
      //--max: 1.111rem;
      //--clamped-font-size: clamp(var(--min), 18px, var(--max));
      @include list-reset;
      font-family: $montserratLight;
      font-size: var(--text-font-size);
      font-weight: 300;
      line-height: 1.728;
      max-width: 700px;
      padding-bottom: var(--padding-bottom);

      @include mobilePortrait {
        font-size: 0.889rem;
      }
      @include mobileLandscape {
        font-size: 0.889rem;
        padding-bottom: 30px;
      }
    }
  }

  &__btn {
    width: 230px;

    svg {
      rect {
        stroke-dasharray: 290, 0;
        stroke-dashoffset: 0;
      }
    }

    span{
      color: var(--primary-color);
    }

    &:hover svg rect,
    &:focus svg rect {
      stroke-dasharray: 140, 540;
      stroke-dashoffset: 416;
    }
  }
}
