.slider {
  &__controls {
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between;
    max-width: 250px;
    width: 100%;
    min-height: 1.5rem;
  }

  &__control {
    @include btn-reset;
    position: relative;

    &:hover,
    &:focus {
      transform: scale(1.2);
    }

    &:active {
      opacity: 0.8;
    }

    &--prev {
      svg {
        width: 52px;
        height: 7px;
        fill: var(--text-color);
      }
    }

    &--next {
      svg {
        width: 155px;
        height: 5px;
        fill: var(--secondary-color);
      }
    }

    &.swiper-button-disabled {
      opacity: 0.5;
      pointer-events: none;
    }
  }
}
