.addition {
  margin-top: 30px;

  &__info {
    display: flex;
  }

  &__description {
    max-width: 850px;
    width: 100%;
    div {
      &:not(:last-of-type) {
        padding-bottom: 40px;
      }

      h4 {
        font-family: $robotoLight;
        font-weight: 300;
        font-size: 1.125rem;
        padding-bottom: 10px;
      }

      p {
        line-height: 1.333;
        max-width: 650px;

        strong {
          font-weight: bold;
        }
      }
    }
  }

  &__reward {
    ul {
      @include list-reset;
      @include flex_column;
      //justify-content: space-between;
      height: 100%;
      //max-width: 400px;

      li {
        display: flex;
        margin-bottom: 30px;

        img {
          margin-right: 15px;
          height: auto;
        }

        p {
          font-size: 0.875rem;
          line-height: 1.508;
          max-width: 300px;

          span {
            display: block;
            strong {
              font-weight: bold;
              //line-height: 1;
            }
          }
        }
      }
    }
  }
}
