.feedback-slider {
  padding-top: 10px;
  padding-bottom: 50px;

  &__item {
    display: flex;
    align-items: flex-start;
    height: auto;
  }

  &__avatar {
    @include flex_column;
    align-items: center;
    margin-right: 80px;

    h4 {
      font-family: $robotoRegular;
      font-weight: 400;
      line-height: 1.5;
      font-size: 1rem;
    }

    p {
      font-family: $robotoLight;
      font-weight: 300;
      line-height: 1.5;
      font-size: 1rem;
    }
  }

  &__image {
    width: 138px;
    height: 138px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    margin-bottom: 30px;
    background-color: #ccd7fd;

    img {
      @include center;
      @include fit-img;
    }
  }

  &__text {
    max-width: 60%;
    width: 100%;

    p {
      font-size: var(--text-font-size);
      line-height: 1.667;
      padding-bottom: 10px;
    }
  }

