.excursion {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  min-height: 643px;
  background-image: url("../img/corks.png");
  background-image: -webkit-image-set(url('../img/corks.webp') 1x);
  background-color: #a8959b;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  overflow: hidden;

  @include retina {
    background-image: url("../img/corks@2x.png");
    background-image: -webkit-image-set(url('../img/corks@2x.webp') 2x);
  }

  &__content {
    @include flex_column;
  }

  &__heading {
    @include list-reset;
    color: var(--primary-color);
    padding-bottom: var(--padding-bottom);
    line-height: 1.25;
  }

  &__text {
    color: var(--primary-color);
    max-width: 500px;
    max-height: 350px;
    overflow: hidden;
    padding-bottom: var(--padding-bottom);

    p {
      @include list-reset;
      padding-bottom: 15px;
      line-height: 1.667;
    }
  }

  &__btn {

    span{
      color: var(--primary-color);
    }
  }
}
