.services-slider {
  padding-bottom: 60px;

  &__item {
    display: flex;
    justify-content: space-between;
    max-width: 935px;
  }

  &__img {
    width: 451px;
    height: 604px;
    position: relative;

    img {
      @include center;
      @include fit-img;
    }
  }

  &__info {
    @include flex_column;
    align-items: flex-end;
    justify-content: center;
    max-width: 410px;
    width: 100%;
    max-height: 604px;
    overflow: hidden;
    text-align: left;
    padding-right: 20px;

    h2, p {
      @include list-reset;
      direction: initial;
    }

    h2 {
      font-family: $robotoSlabRegular;
      font-weight: 400;
      font-size: var(--heading-font-size);
      color: var(--secondary-color);
      line-height: 1.25;
      padding-bottom: var(--padding-bottom);
    }
  }

  &__text {
    padding-bottom: var(--padding-bottom);

    p {
      font-family: $robotoLight;
      font-weight: 300;
      font-size: var(--text-font-size);
      line-height: 1.667;
      color: var(--text-color);
    }
  }

  &__btn {
    color: var(--secondary-color);

    span{
      color: var(--secondary-color);
    }
  }

  &__controls {
    //position: absolute;
    //bottom: 0;
    //left: 50%;
    //transform: translateX(-50%);
    //display: flex;
    //justify-content: space-between;
    //max-width: 250px;
    //width: 100%;
    //min-height: 1.5rem;

    button {
      &:first-child {
        transform: rotate(180deg);

        &:hover,
        &:focus {
          transform: rotate(180deg) scale(1.2);
        }
      }

      &:last-child {
        transform: rotate(-180deg);

        &:hover,
        &:focus {
          transform: rotate(-180deg) scale(1.2);
        }
      }
    }
  }

